export default {
	data() {
		return {
			billInfo:null,
			billDetailInfo:null,
			pageNum:1,
			dataType:null,
			billDetailList:null,
			modalName: null
			
		}
	},
	methods: {
		
		loadData()
		{
			var me = this
			if(this.dataType==0)
			{
				// uni.request({
				// 	url: this.serverUrl + '/sign_up/bill_info/un_complete/' + this.pageNum + '?name=&limit=1',
				// 	method: 'GET',
				// 	data: {},
				// 	success: res => {
				// 		me.billInfo = res.data[0]
				// 	},
				// 	fail: () => {},
				// 	complete: () => {}
				// });
				uni.request({
					url: this.serverUrl + '/sign_up/bill_info/un_complete/' + this.pageNum + '?name=&limit=1'
				})
				.then(data =>{
					var [error,res]  = data;
					me.billInfo  = res.data[0]
					this.getBillDetailList()
				})
			}
			else
			{
				// uni.request({
				// 	url: this.serverUrl + '/sign_up/bill_info/complete/' + this.pageNum + '?name=&limit=1',
				// 	method: 'GET',
				// 	data: {},
				// 	success: res => {
				// 		me.billInfo = res.data[0]
				// 	},
				// 	fail: () => {},
				// 	complete: () => {}
				// });
				uni.request({
					url: this.serverUrl + '/sign_up/bill_info/complete/' + this.pageNum + '?name=&limit=1'
				})
				.then(data =>{
					var [error,res]  = data;
					me.billInfo  = res.data[0]
					this.getBillDetailList()
				})
				
			}			
		},
		next()
		{
			this.pageNum++;
			this.loadData()
		},
		
		prevent()
		{
			if(this.pageNum>1)
			{
				this.pageNum--;
				this.loadData()
			}
		},
		
		getBillDetailList()
		{
			uni.request({
				url: this.serverUrl + '/sign_up/bill_details/info/json/' + this.billInfo.id,
				method: 'GET',
				data: {},
				success: res => {
					this.billDetailList = res.data
					console.log(this.billDetailList.length)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		showModel()
		{
			this.modalName = 'Image'
		},
		hideModal()
		{
			this.modalName = null
		}
		
	},
	onLoad(e) {
		this.dataType = e.type
		this.loadData()
	}
}
